.App {
	background-color: #424B54;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
    overflow-x: hidden;
}

.page {
	height: 100vh;
	width: 100vw;
	display: flex;
	font-size: 5vmin;
	font-family: 'Courier New', Courier, monospace;
	justify-content: center;
	align-items: center;
    margin: 0;
	position: relative;
}

html {
	scroll-behavior: smooth;
  }

/* first page */

#introduction {
    color: white;
    text-align: center;
    width: 75vw;
}

.custom-shape-divider-bottom-1725843514 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-bottom-1725843514 svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 200px;
}

.custom-shape-divider-bottom-1725843514 .shape-fill {
    fill: #EBCFB2;
}

.App::-webkit-scrollbar {
	display: none;
	-ms-overflow-style: none;
  	scrollbar-width: none;
	overflow-x: hidden;
  }

.down-arrow-wrapper {
	position: absolute;
	top: 80vh;
	animation: jumpInfinite 2.5s infinite;
}
  
.down-arrow {
	content: "    ";
	position: absolute;
	top: 12px;
	left: -10px;
	width: 16px;
	height: 16px;
	border-bottom: 2px solid;
	border-right: 2px solid;
	transform: rotateZ(45deg);
	opacity: 50%;
	border-color: white;
}

