#page2{
	background-color: beige;
	height: 200vh;
}

.printing_animation {
    margin-left: 10rem;
    width: 40rem;
    height: 50rem;
    background-color: white;   
    outline-style: double;
    outline-color: black;
    outline-width: 1px;
    position: relative;
    animation: printing 5s forwards ease-out;
}

@keyframes printing {
    0% {
        left: 100%;
    }   
    9% {
        left: 100%;
    }
    10%{
        left: 90%;
    }   

    20% {
        left: 89%;
    }
    21%{
        left: 80%;
    } 
    
    40% {
        left: 78%;
    }
    41%{
        left: 70%;
    }

    60% {
        left: 69%;
    }

    100%{
        left: 0%;
    }

}

.selfie_container {
    outline-style: double;
    outline-color: black;
    outline-width: 2px;
    align-content: center;
}

.printer {
    background-color: black;
    width: 0.7rem;
    height: 52rem;;
    z-index: 1;
    border-radius: 4px;
}

.printing_container {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}


